<template>
  <b-nav>
    <b-navbar-brand to="/" class="mr-0">
      <b-img
        src="@/assets/img/geokom.svg"
        alt="Logo Komputasi Geologi"
        height="50px"
        class="p-2" />
    </b-navbar-brand>
    <b-nav-text class="my-auto">
      {{header}}
    </b-nav-text>
    <b-navbar-nav class="ml-auto my-auto mr-2" >
      <b-nav-dropdown text="Profile" v-if="logged" right>
        <b-dropdown-item @click=handleLogout()>Logout
        </b-dropdown-item>
      </b-nav-dropdown>
      <b-nav-dropdown text="User" v-else right>
        <b-dropdown-item to="/auth/register">Register
        </b-dropdown-item>
        <b-dropdown-item to="/auth/login">Login
        </b-dropdown-item>
      </b-nav-dropdown>
    </b-navbar-nav>
  </b-nav>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      header: 'common/header',
      logged: 'auth/isLoggedIn'
    })
  },
  methods: {
    ...mapActions({
      logout: 'auth/LOGOUT'
    }),

    handleLogout () {
      this.logout()
      this.$router.push({ name: 'home'})
    }
  }
}
</script>

<style lang="scss" scoped>
.nav {
  background-color: lightgray;
}
</style>