import Auth from "@/pages/auth/index.vue";
import Register from "@/pages/auth/register.vue";
import Login from "@/pages/auth/login.vue";

const router = [
  {
    path: '/auth',
    name: 'Auth',
    component: Auth,
    children: [
      {
        path: 'register',
        name: 'Register',
        component: Register,
      },
      {
        path: 'login',
        name: 'Login',
        component: Login,
      },
    ]
  }
]

export default router;