import * as types from './authTypes'

export default {
  [types.LOGIN] (state) {
    state.isLogin = true
    state.loginError = null
  },

  [types.LOGIN_SUCCESS] (state, data) {
    state.isLogin = false
    state.user = data.user
    state.permits = data.user.role.permits
    state.isLoggedIn = true

    // Set localStorage
    window.localStorage.setItem('token', `Token ${data.token}`)
    window.localStorage.setItem('user', data.user.name )
    window.localStorage.setItem('permits', data.user.role.permits )

  },

  [types.LOGIN_ERROR] (state, error) {
    state.isLogin = false
    state.loginError = error
  },

  [types.LOGOUT] (state) {
    window.localStorage.removeItem('token')
    window.localStorage.removeItem('user')
    window.localStorage.removeItem('permits')
    state.user = {}
    state.permits = []
    state.isLoggedIn = false
  },

  [types.REGISTER] (state) {
    state.isRegister = true
    state.registerError = null
  },

  [types.REGISTER_SUCCESS] (state, data) {
    state.isRegister = false
    state.user = data
  },

  [types.REGISTER_ERROR] (state, error) {
    state.isRegister = false
    state.registerError = error
  },
}